@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(22, 22, 22);
}

.banner {
  background-image: linear-gradient(rgba(0, 0, 0, 0.633), rgba(0, 0, 0, 0.578)), url("/public/images/bg-X1.jpg");
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(56, 56, 56, 0.623);
  background-color: antiquewhite;
}

::-webkit-scrollbar-thumb {
  background: rgb(228, 0, 0);
}